.contentHomePage {
    width: 100%;
    height: calc(100vh - 12vh - env(safe-area-inset-top));
    display: flex;
    padding: 1rem 1rem 1.5rem 1rem;
    flex-direction: column;
    gap: 1.5rem;
    position: fixed;
    top: env(safe-area-inset-top);
    left: 0;
    // touch-action:none;

    & .content {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        gap: 1rem;
        
    }
}

.welcomeMessage {
    font-size: 1.625rem;
    font-weight: 700;
    font-family: "DM Sans";
    color: var(--primary-text);

    & span {
        color: var(--main-color);
    }
}

.headerHomePage {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 1rem;

    &__links {
        display: flex;
        justify-content: space-between;
        align-items: center;

        & img {
            height: 2.5rem;
            aspect-ratio: 1/1;
            object-fit: cover;
            border-radius: 100%;
        }
    }
}

.notificationsButton{
    color: var(--primary-text);

    & circle{
        fill: var(--main-color);
        stroke: var(--background);
    }
}



.newGrades {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    

    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;

        & h2 {
            color: var(--primary-text);
            font-size: 1.125rem;
            font-weight: 700;
        }
    }

    &__grid{
        display: inline-flex;
        gap: .25rem;
        overflow-x: scroll;
        width: 100%;
        scroll-snap-type: x mandatory;
        
    }

    &__item{
        scroll-snap-align:start;
        display: flex;
        min-width: calc(50% - .25rem);
        flex: 1;
        padding: 0 1rem;
        aspect-ratio: 176/72;
        gap: .5rem;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: var(--color-card);
        border-radius: .5rem;
    
        & h4{
            color: var(--disabled-text);
            font-size: .875rem;
            font-weight: 400;
            line-height: 1;
            white-space: nowrap;
            max-width: 12ch;
            overflow: hidden;
            text-overflow: ellipsis;
        }

        & h3{
            color: var(--primary-text);
            font-size: 1rem;
            font-weight: 600;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            max-width: 12ch;
            line-height: 1rem;
        }
    }
}



