.averages{
    display: flex;
    flex-direction: column;
    gap: .5rem;
    height: 100%;

    &__header{
        display: flex;
        justify-content: space-between;
        align-items: center;

        & h2{
            color: var(--primary-text);
            font-family: "DM Sans";
            font-size: 1.125rem;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
        }

        & a {
            color: var(--disabled-text, rgba(29, 36, 51, 0.65));
            font-family: "DM Sans";
            font-size: 1rem;
            font-style: normal;
            font-weight: 500;
            line-height: normal;
            text-decoration: none;
        }
    }
}

.averagesGrid{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-template-rows: repeat(3,auto);
    width: 100%;
    height: 100%;
    gap: .5rem;
}
