



.ueComponent{
    width: 100%;
    display: flex;
    height: auto;
    flex-direction: column;
    // overflow: hidden;
    border-radius: .5rem;
    scroll-snap-align: start;
    

    &__header{
        height: 3rem;
        width: 100%;
        background: var(----color-header-ue, #11241C);
        color: var(--background);
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: .75rem;
        transition: border-radius .6s ease-out;
        border-radius: .5rem .5rem 0 0;
        

        &.open{
            border-radius: .5rem .5rem 0 0;
        }

        &.close{
            border-radius: .5rem;
        }
    }

    &.open{

        & .ueComponent__header svg{
            transform:rotate(180deg);
        }
        
    }

    &.close{
        & header svg{
            transform:rotate(0deg);
        }
    }
}

.headerUeComponent{
    & svg{
        transition: transform .4s ease-in-out;
    }

    &__ueName{
        font-size: 0.875rem;
        font-weight: 600;
        line-height: 1rem; /* 114.286% */
    }

    &__ueAverage{
        padding: .375rem .5rem;
        background: rgba(20, 121, 90, 0.40);
        border-radius: .5rem;
        font-size: 0.875rem;
        font-weight: 700;
        line-height: 1rem; /* 114.286% */
    }

    &__details{
        display: flex;
        gap: .5rem;
        align-items: center;
    }
}

.contentWrapper{
    display: grid;
    width: 100%;
    border-radius: .5rem;
    grid-template-rows: 0fr;
    transition: all .5s ease-out;

    & .subjectGrid{
        height: 100%;
    }

    &.open{
        grid-template-rows: 1fr;
        padding: .25rem 0;
    }

    &.close{
        grid-template-rows: 0fr;
        padding: 0;
    }
}

.subjectGrid{
    width: 100%;
    height: 100%;
    grid-column: 1;
    display: flex;
    flex-direction: column;
    gap: .5rem;
    overflow: hidden;
    // padding: .25rem 0;

    & .itemSubject{
        width: 100%;
        padding: .625rem .75rem;
        display: flex;
        flex-direction: column;
        gap: .5rem;

        &__header{
            display: flex;
            justify-content: space-between;
            align-items: center;

            & .subjectName{
                color: var(--primary-text, #11241C);
                font-size: 0.875rem;
                font-weight: 600;
                line-height: 1;
            }

            & .detailsSubject{
                display: flex;
                align-items: center;
                gap: 1rem;

                & .coefficient{
                    color: var(--second-text);
                    font-size: 0.75rem;
                    font-weight: 600;
                    line-height: 1;
                }

                & .subjectAverage{
                    color: var(--second-text);
                    font-size: 0.75rem;
                    font-weight: 900;
                    line-height: 1;
                }
            }
        }

        
    }
}

.gradesGrid{
    max-width: 100rem;
    display: flex;
    gap: 1rem;
    overflow-x: scroll;
    padding-top: 1rem;
    scroll-snap-type: x mandatory;

    &.empty{
        padding: 0;
    }
    

    &__item{
        padding: .625rem 1.3rem;
        background-color: var(--color-card);
        position: relative;
        border-radius: .5rem;
        scroll-snap-align: start;


        & .grade{
            color: var(--second-text);
            font-size: 0.75rem;
            font-style: normal;
            font-weight: 500;
            line-height: 1rem; /* 133.333% */
        }
      

        & .coefficientGrade{
            background: var(--color-coefficient, #3F4F47);
            display: flex;
            width: auto;
            position: absolute;
            z-index: 99;
            right: 0;
            top: 0;
            border-radius: .1875rem;
            padding: .3rem .3rem;
            transform: translate(25%,-50%);
            & p{
                color: var(--background);
                font-size: 0.625rem;
                font-weight: 600;
                line-height: 1; /* 160% */
            }
        }
    }
}