.ueCard{
    width: 100%;
    height: 100%;
    background-color: var(--color-card);
    border-radius: .5rem;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: end;
    overflow: hidden;

    &__background{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.contentCard{
    background: linear-gradient(180deg, rgba(17, 36, 28, 0.39) 1.5%, rgba(17, 36, 28, 0.80) 53%);
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    padding: .5rem;
    display: flex;
    justify-content: start;
    align-items: end;

    &__pill{
        padding: .2rem .5rem;
        background: rgba(255, 255, 255, 0.20);
        position: absolute;
        z-index: 2;
        top: .5rem;
        left: .5rem;

        & p{
            color: var(----color-border-higher, #D7E6DF);
            font-size: 0.875rem;
            font-weight: 900;
            line-height: 1.25rem;
        }
    }

    &__texts{
        & .title{
            overflow: hidden;
            color: var(----color-text-image, #F4F8F6);
            font-size: 0.875rem;
            font-weight: 600;
            line-height: 1.0625rem; /* 121.429% */
        }

        & .grade{
            color: #40B890;
            font-size: 0.875rem;
            font-weight: 900;
            line-height: 1.25rem; /* 142.857% */

            &.null{
                color: #A3A3A3;
            }
        }
    }
}