:root {
    /* colors */
    --background: #f4f8f6;
    --color-border--lighter: #ebf2ef;
    --color-border-higher: #d7e6df;
    --color-card: #edf1ef;
    --color-input: #9d9d9d;
    --color-select: #7e807f;
    --color-text-input: #133428;
    
    --main-color: #134F3B;
    
    --primary-text: #11241c;
    --second-text: #11241ccc;
    --third-text: #11241ca6;
    --disabled-text: #11241c66;
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-family: "DM Sans", sans-serif;
    
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    // font-family: 'Playfair Display', serif;
    scrollbar-width: none;
  /*IE10+*/
    -ms-overflow-style: -ms-autohiding-scrollbar;
}

*::-webkit-scrollbar {
    -webkit-appearance: none;
    width: 0;
    height: 0;
    display: none;
}

html,
body,
#root,
.wrapper {
    width: 100%;
    height: 100lvh;
    
}

html,body{
    background-color: var(--background);
}

#root {
    padding-top: env(safe-area-inset-top);
    background-color: var(--background);
    height: 100vh;
}

.onboarding{
    padding-top: env(safe-area-inset-top) ;
}


.onboarding__texts{
    padding: 0 1.5rem;
    width: 100%;

    & h2{
        color: var(--main-color, #14795A);
        font-size: 1.825rem;
        font-style: normal;
        font-weight: 900;
        line-height: normal;
    }

    & h1{
        color: var(--text-icon-primary-black, #1D2433);
        font-size: 2.1875rem;
        font-style: normal;
        font-weight: 600;
        line-height: 2.84375rem; /* 130% */
    }
}


.popupOnboarding{
    width: 100%;
    height: 65vh;
    background-color: var(--main-color);
    position: fixed;
    bottom: 0;
    border-radius: 4rem 4rem 0 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 1.5rem calc(env(safe-area-inset-bottom) + 1rem) 1.5rem;

    & img{
        width: 90%;
    }

    &__header{
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 1rem;

        & .pill{
            width: 3.6rem;
            aspect-ratio: 4.6/0.5;
            border-radius: 10rem;
            background-color: #16523E;
        }

        & h2{
            color: var(--Neutral-Base-Background-Light, #FFF);
            font-family: "DM Sans";
            font-size: 1.5rem;
            font-style: normal;
            font-weight: 700;
            line-height: 1.75rem; /* 116.667% */
        }
    }

    &__footer{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;

        & .nextButton{
            display: flex;
            padding: .75rem 1.25rem;
            align-items: center;
            border-radius: 0.5rem;
            background: var(--Neutral-Base-Background-Light, #F4F8F6);

            & p {
                color: var(--main-color, #14795A);
                font-weight: 700;
            }
        }
    }
}













.progressBar{
    width: 100%;
    background-color: #eceef0;
    height: .25rem;
    border-radius: 3rem;
    overflow: hidden;

    &_current{
        background-color: #047857;
        width: 30%;
        height: 100%;
        transition: width .5s ease-in-out;
    }
}

.inputs{
    gap: 1rem;
    display: flex;
    flex-direction: column;
}

