.buttons{
    display: flex;
    flex-direction: column;
    gap: 1rem;

    & .line{
        height: .05rem;
        width: 100%;
        background-color: #94A3B8;
        position: relative;

        & p {
            position: absolute;
            background-color: #fafafa;
            top: 50%;
            left: 50%;
            padding: 0 .75rem;
            transform: translate(-50%,-50%);
            font-size: .75rem;
            color: #1D2433A6;
            font-weight: 600;
        }
    }
}

