.overlay{
    position: fixed;
    width: 100%;
    height: calc(100% );
    background-color: rgba(0, 0, 0, .8);
    z-index: 99999;
    top: 0;
    left: 0;
    display: flex;
    align-items: end;
    overflow-y: hidden;
    animation: fadeInOverlay .2s ease-in;
}

.popup{
    width: 100%;
    background-color: var(--background);
    border-radius: 2rem 2rem 0 0;
    // transition: transform .2s ease-in;
    position: relative;
    padding: 2.1875rem 1rem 2.5rem 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.5rem;
    &__title{
        color: var(----primary-text, #11241C);
        font-size: 1.5rem;
        font-weight: 900;
        line-height: 1.75rem; /* 116.667% */
    }

    

    &.open{
        animation: open .3s ease-out;
    }

    &.close{
        animation: close .3s ease-out;
    }

    & .pill{
        height: .3rem;
        width: 3rem;
        background-color: var(--color-border-higher);
        border-radius: 100rem;
        position: absolute;
        top: 1rem;
        left: 50%;
        transform: translate(-50%,-50%);
    }
}

@keyframes open {
    from{
        transform: translateY(100%);
    }

    to {
        transform: translateY(0%);
    }
}

@keyframes close {
    from{
        transform: translateY(0%);
    }

    to {
        transform: translateY(100%);
    }
}

@keyframes fadeInOverlay {
    from{
        background-color: rgba(0, 0, 0, 0);
    }

    to {
        background-color: rgba(0, 0, 0, .8);
    } 
}

@keyframes fadeOutOverlay {
    from{
        background-color: rgba(0, 0, 0, .8);
    }

    to {
        background-color: rgba(0, 0, 0, 0);
    } 
}

.contentPopup{
    width: 100%;
    height: 100%;

    & .cta{
        background-color: var(--main-color);
        border-radius: .5rem;
        width: 100%;
        padding: 1rem 0;
        color: var(----color-text-cta, #F4F8F6);
        font-size: 1rem;
        font-weight: 600;
        line-height: normal;
    }
}

.notificationsGrid{
    display: grid;
    flex-direction: column;
    gap: 0.75rem;
    height: auto;
    max-height:24rem;
    overflow-y: scroll;
    scroll-snap-type: y mandatory;

    & .emptyLabel{
        padding: 3rem 0;
        text-align: center;
    }

    &__item{
        width: 100%;
        padding: 1rem 1.5rem;
        background: var(--color-card);
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-self: center;
        border-radius: 0.625rem;
        scroll-snap-align: start;

        & p {
            color: var(----second-text, rgba(17, 36, 28, 0.80));
            font-size: 0.875rem;
            font-weight: 500;
            line-height: 1.125rem; /* 128.571% */
        }
    }
}

.addGradeForm{
    display: flex;
    flex-direction: column;
    gap: 1rem;

    & .grade{
        display: flex;
        width: 100%;
        justify-content: center;
        gap: .2rem;
    }
}

.gradeInput{
    border: none;
    background: transparent;
    color: var(----disabled-text);
    font-size: 2.25rem;
    width: 50%;
    text-align: center;
    outline: none;
}

.gradeAdded{
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: column;
    gap: 1.25rem;
    &__icon{
        height: 8rem;
        aspect-ratio: 1/1;
        background-color: var(--color-card);
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 100%;
        font-size: 3rem;
        animation: icon .5s ease-in-out;
    }

    &__message{
        color: var(--primary-text);
        text-align: center;
        font-size: 1.5rem;
        font-weight: 900;
        line-height: 1.75rem; /* 116.667% */
        letter-spacing: -0.03rem;
    }
}

.gradeDetailsPopup{
    gap: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;

    & .detailsGrade{
        display: flex;
        flex-direction: column;
        width: 100%;
        align-items: center;
        gap: .5rem;

        &__testName{
            color: var(--primary-text);
            font-size: 1.5rem;
            font-weight: 900;
            line-height: 1.5rem; /* 100% */
        }

        &__subjectName{
            color: var(--second-text);
            font-size: 1rem;
            font-weight: 600;
            line-height: 1rem; /* 100% */
        }

        &__testCoefficient{
            color: var(--third-text);
            font-size: .9rem;
            font-weight: 600;
            line-height: .8rem; /* 100% */
        }
    }

    & .gradeValue{
        display: flex;
        justify-content: center;
        align-items: center;
        & h1 {
            color: var(--second-text);
            font-size: 2.25rem;
            font-weight: 900;
            line-height: normal;
        }
       
    }

    & .actionsGrade{
        display: flex;
        width: 100%;
        gap: .5rem;

        &__item{
            display: flex;
            gap: .5rem;
            background-color: var(--color-card);
            width: 100%;
            padding: 1rem 0;
            align-items: center;
            justify-content: center;
            border-radius: .5rem;
            color: var(--second-text);

            & svg{
                height: 1rem;
                aspect-ratio: 1/1;
            }

            & p {
                
                font-size: 1rem;
                font-weight: 600;
            }

            &.editGrade{
                background: var(--main-color);
                color: var(----color-text-cta, #F4F8F6);
            }

            &.deleteGrade{
                background: var(--Danger-800, #BA2532);
                color: var(----color-text-cta, #F4F8F6);
            }
        }
    }
}

@keyframes icon {
    from{
        font-size: 0rem;
    }

    to{
        font-size: 3rem;
    }
}