.contentProfilPage{
    width: 100%;
    height: calc(100vh - 12vh - env(safe-area-inset-top));
    display: flex;
    padding: 1rem 1rem 1.5rem 1rem;
    flex-direction: column;
    gap: 2rem;
    position: fixed;
    top: env(safe-area-inset-top);
    left: 0;
    overflow: hidden;
    z-index: 10;

    &__header{
        display: flex;
        justify-content: space-between;
        align-items: center;

        & h1{
            color: var(----primary-text, #11241C);
            font-size: 1.5625rem;
            font-weight: 700;
        }

        & svg{
            width: 1.3rem;
            aspect-ratio: 1/1;
        }
    }
}

.halo{
    width: 14.1875rem;
    aspect-ratio: 2/1;
    border-radius: 20.1875rem;
    opacity: 0.9;
    background: var(--main-color);
    filter: blur(50px);
    position: absolute;
    top: 6rem;
    left: 50%;
    transform: translateX(-50%);
}
.userDetails{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;

    &__texts{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    & .profilePicture{
        height: 8rem;
        aspect-ratio: 1/1;
        border-radius: 50%;
        position: relative;

        & img{
            width: 100%;
            height: 100%;
            border-radius: 50%;
            object-fit: cover;
        }

        & .editPill{
            
            height: 2rem;
            aspect-ratio: 1/1;
            background-color: var(--main-color);
            background-color: red;
            transform: translateX(-25%);
            border-radius: 50rem;
            position: absolute;
            display: flex;
            align-items: center;
            justify-content: center;
            bottom: 0;
            right: 0;

            & svg{
                height: 1rem;
                aspect-ratio: 1/1;
            }
        }
    }
}

.settingsGrid{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-template-rows: repeat(3,1fr);
    height: 100%;
    gap: .5rem;

    &__item{
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: var(--color-card);
        border-radius: .5rem;
        justify-content: center;
        gap: 1rem;
        padding: 0 2rem;

        & svg{
            height: 1.5rem;
            aspect-ratio: 1/1;
            color: var(--main-color);
        }

        & p {
            color: var(----second-text);
            text-align: center;
            font-size: .9rem;
            font-weight: 500;
            line-height: 1rem; /* 114.286% */
        }
    }
}