.contentNotesPage{
    width: 100%;
    height: calc(100vh - 12vh - env(safe-area-inset-top));
    display: flex;
    padding: 1rem 1rem 1.5rem 1rem;
    flex-direction: column;
    gap: 1rem;
    position: fixed;
    top: env(safe-area-inset-top);
    left: 0;
    overflow: hidden;
    z-index: 10;

    &__header{
        display: flex;
        justify-content: space-between;
        align-items: center;

        & h1{
            color: var(----primary-text, #11241C);
            font-size: 1.5625rem;
            font-weight: 700;
        }

        & svg{
            width: 1.5rem;
            height: 1.5rem;
        }
    }

    & .content {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        gap: 1rem;
        
    }
}

.actionButtons{
    width: 100%;
    display: flex;
    gap: .5rem;

    &__item{
        width: 100%;
        padding: 1rem .75rem;
        background-color: var(--color-card);
        border-radius: .5rem;
        display: flex;
        gap: .5rem;
        align-items: center;
        justify-content: center;

        & svg{
            height: 2.5rem;
            aspect-ratio: 1/1;
            color: var(--main-color);
        }

        & p {
            color: var(--second-text);
            font-size: 0.875rem;
            font-weight: 500;
            line-height: 1rem;
        }
    }
}

.semesterAverage{
    & p {
        color: var(--second-text);
        font-size: 1.125rem;
        font-weight: 500;
        line-height: normal;
    }

    & strong{
        color: var(--main-color);
        font-size: 1.125rem;
        font-weight: 600;
        line-height: normal;
    }
}


.ueList{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: .75rem;
    overflow-y:scroll;
    padding-bottom: 3rem;
}
