.welcome {
    padding: calc(env(safe-area-inset-top) * 0.5) 1.5rem 0 1.5rem;
}

.username,
.password {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;

    & .message {
        color: var(--primary-text);
        font-size: 1.5rem;
        font-style: normal;
        font-weight: 500;
        line-height: 1.5rem; /* 100% */
        letter-spacing: -0.03rem;

        & .name {
            color: var(--main-color);
        }
    }
}

.ctaButton {
    display: flex;
    width: 100%;
    padding: 1rem 1.25rem;
    justify-content: center;
    align-items: center;
    border-radius: var(--border-radius, 0.5rem);

    animation: fadeIn 0.5s ease-in-out;
    & p {
        font-weight: 600;
    }

    &.disabled {
        background: #edf1ef;
        color: #d6dad8;
    }

    &.enabled {
        background: var(--main-color);
        color: var(--background);
    }
}

.navigation {
    display: flex;
    justify-content: space-between;
    position: fixed;
    bottom: calc(env(safe-area-inset-bottom) * 1.5);
    width: calc(100% - 3rem);

    &__previous {
        height: 3.375rem;
        aspect-ratio: 1/1;
        border-radius: var(--border-radius, 0.5rem);
        background: #edf1ef;
        display: flex;
        justify-content: center;
        align-items: center;

        & svg {
            height: 1.5rem;
        }
    }

    & .ctaButton {
        animation: none;
        width: auto;
        height: 3.375rem;
    }
}

.indicators {
    display: flex;
    gap: 0.5rem;
    width: 14rem;
    aspect-ratio: 14 / 0.375;

    &__pill {
        width: 100%;
        height: 100%;
        background: var(--color-card);
        border-radius: 10rem;
        transition: background-color 0.3s ease-in-out;

        &.active {
            background: var(--main-color);
        }
    }
}

.inputs {
    gap: 1.5rem;
    margin-top: 0.5rem;
}

.profile {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;

    &__name,
    &__id {
        line-height: 1;
    }
    &__name {
        margin-top: 0.5rem;
        font-size: 1.375rem;
        line-height: 1.375rem; /* 100% */
    }

    &__id {
        color: var(--third-text);
        font-size: 1rem;
        font-weight: 400;
    }

    & input {
        display: none;
    }

    & .profileTexts {
        display: flex;
        flex-direction: column;
        gap: 0.5rem;
    }

    & .profilePicture {
        height: 4rem;
        aspect-ratio: 1/1;
        border-radius: 100%;
        position: relative;

        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 100%;
        }

        &__edit,
        &__delete {
            position: absolute;
            bottom: 0;
            right: 0;
            background-color: #f5f5f5;
            height: 1.5rem;
            width: 1.5rem;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 10rem;
            border: 0.5rem solid var(--background);
            transform: translate(25%, 25%);
            color: #047857;
            & svg {
                height: 1.2rem;
            }
        }

        &__delete {
            bottom: 0;
            left: 0;
            background-color: #e02d3c;
            color: white;
            transform: translate(0%, 25%);

            & svg {
                height: 1rem;
            }
        }
    }
}


.options{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: .5rem;
    margin-top: .5rem;
    box-sizing: border-box;
    flex-shrink: 1;
    flex: 0 0 0;

    &__item{
        display: flex;
        padding: 1.25rem 0.875rem;
        width: 100%;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 0.5rem;
        border-radius: 0.5rem;
        background: var(--color-card, #EAEEEC);

        & h1{
            font-size: 1rem;
            color: var(--color-text-input);
        }

        & h2{
            color: var(--second-text);
            text-align: center;
            font-family: "DM Sans";
            font-size: 0.875rem;
            font-style: normal;
            font-weight: 500;
            line-height: 1rem; /* 114.286% */
        }
    }
}