select{
    width: 100%;
    height: 3.1rem;
    font-size: 1rem;
    border-radius: .5rem;
    border: .1rem solid var(--color-border-higher);
    background-color: transparent;
    padding: 0 .75rem;
    color: var(--color-text-input);
    outline: none;
    background-image: url("../../public/select.svg");
      background-position: calc(100% - .75rem) center; /*Positioning*/
      background-repeat: no-repeat; /*Prevent showing multiple background images*/
}