.navbar{
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 9;
    width: 100%;
    height: 12vh;
    display: flex;
    padding: 0 5vh 2.5vh 5vh;
    justify-content: space-between;
    align-items: center;
    border-top: .1rem solid #E1E6EF;
    color:  rgba(29, 36, 51, 0.80);

    &__link{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: .2rem;
        width: 20%;

        & .pictureBox{
            height: 2rem;
            aspect-ratio: 1/1;
            display: flex;
            align-items: center;
            justify-content: center;
            & img{
                height: 1.5rem;
                aspect-ratio: 1/1;
                object-fit: cover;
                border-radius: 100%;
            }
            
        }

        svg{
            height: 2rem;
        }

        & p {
            font-size: .75rem;
            font-weight: 400;
            color: var(----third-text, rgba(17, 36, 28, 0.65));
        }

        &.active{
            & svg{
                color: var(--main-color);
            }
            & p {
                color: #1D2433;
                font-weight: 700;
            }
            
        }
    }
}

